
import { defineComponent } from "vue";
import store from "@/store/store";

export default defineComponent({
  data() {
    return {
      state: store.state,
    };
  },

  computed: {
    lastLoadedDay(): string {
      return store.prettyDate(
        this.state.activities[this.state.activities.length - 1].day
      );
    },
  },
});
